.App {
  background: linear-gradient(to bottom, #ffecdb, #ffab91);
  height: auto;
  min-height: 200vh;
  display: flex;
  justify-content: flex-start;
  align-items: center; /* 수평 중앙 정렬 */
  flex-direction: column;
  padding: 3rem 1rem;
  padding-top: calc(3rem + 4rem);
  overflow-y: auto; /* 세로 스크롤 활성화 */
  margin: 0;
  font-family: "KimjungchulMyungjo-Regular";
}

.section {
  opacity: 0;
  transform: translateY(20px);
  transition: opacity 1s ease, transform 1s ease;
}

.section.visible {
  opacity: 1;
  transform: translateY(0);
}

.ContactDetail {
  width: 100%;
  padding-bottom: 20px; /* Contact 섹션에 여백 추가 */
  box-sizing: border-box;
  min-height: 300px;
  overflow-y: auto; /* 스크롤 가능하게 */
}

@media screen and (max-width: 768px) {
  .App {
    padding: 2rem 1rem; /* 작은 화면에 맞는 패딩 설정 */
  }

  .project-card,
  .ContactDetail {
    width: 100%; /* 작은 화면에서는 가로 너비를 100%로 */
    height: auto; /* 자동 높이로 조정 */
  }
}
